/*-------- Page-progress --------*/

.page-progress {
    display: flex;
    justify-content: center;
    height: 100%;
    position: fixed;
    left: 100px;
    top: 0;
    z-index: 30;

    &__bar {
        display: flex;
        justify-content: center;
        position: relative;
        width: 1px;
        height: 100%;
        background-color: #3e3e3e;
        opacity: 0.35;
    }

    &__track {
        width: 2px;
        height: 0;
        background-color: #2998e5;
        position: absolute;
        top: 0;
        left: 0;
        transition: height 0.1s ease-out;
    }

    &__image-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 80px;
        height: 80px;
        position: absolute;
        right: calc(100% + 10px);
        top: 20px;
        transition: top 0.1s ease-out;
    }

    &__image {
        width: 100%;
        height: 100%;
        transition: transform 0.1s ease-out;
    }

    &__count {
        font-family: $font-pf;
        font-size: 30px;
        color: #000000;
        position: absolute;
    }
}